.capstones{
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 7.5%;
}

.capstone-card{
  position: relative;
  box-sizing: border-box;
  margin-bottom: 5%;
  cursor: pointer;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  .capstone-image{
    background-size: cover;
    background-position: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 0.25s;
    filter: grayscale(100%);
    opacity: 0.4;
    -webkit-box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
    -moz-box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
    box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
  }
  &:hover .capstone-image{
    filter: none;
    opacity: 0.8;
    transform: scale(1.1);
  }
  h1, p{
    position: absolute;
  }
  h1{
    bottom: 25%;
    font-size: 3.5rem;
  }
  p{
    bottom: 17.5%;
    width: 75%;
    line-height: 1.25rem;
    font-size: 0.9rem;
    font-weight: 400;
    transition: opacity 0.25s;
  }
  &:nth-child(odd){
    margin-right: 7.5%;
    h1{
      right: -10%;
    }
    p{
      right: -9.5%;
      text-align: right;
    }
  }
  &:nth-child(even){
    margin-left: 7.5%;
    margin-top: 7.5%;
    h1{
      left: -10%;
    }
    p{
      left: -9.5%;
      text-align: left;
    }
  }
}
